.node {
  border: solid 1px white;
  font: 10px sans-serif;
  line-height: 12px;
  overflow: hidden;
  position: absolute;
  text-indent: 2px;
}

.Tooltip {
  border: 1px solid #757575;
  box-shadow: none;
  display: table;
  position: absolute;
  padding: 8px;
  text-align: left;
  font-size: 16px;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  pointer-events: none;
  white-space: nowrap;
}

